import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { plus, minus } from "./redux/actions";
import Reset from "./redux/reset";

function App(props) {
  const counter = useSelector((state) => state);
  const dispatcher = useDispatch();

  return (
    <div className="common">
      {counter}
      <div>
        <button onClick={() => dispatcher(plus)} className="btn">
          +
        </button>
        <button onClick={() => dispatcher(minus)} className="btn2">
          -
        </button>
        <Reset />
      </div>
    </div>
  );
}

export default App;
